.mask {
    position: fixed;
    z-index: 1400;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(255, 255, 255, 0.7);
}

.loading {
    position: absolute;
    top: 40%;
    left: 0;
    right: 0;
    transform: translate(0, -50%);
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.info {
    font-size: 26px;
    font-weight: bold;
    line-height: 60px;
}
